@font-face {
  font-family: 'VT323';
  font-style: normal;
  font-weight: 400;
  src: local('VT323 Regular'), local('VT323-Regular'), url("fonts/VT323-latin.woff2") format('woff2');
  unicode-range: U+0000-00FF, U+0131, U+0152-0153, U+02BB-02BC, U+02C6, U+02DA, U+02DC, U+2000-206F, U+2074, U+20AC, U+2122, U+2212, U+2215;
}
.VT323,
body {
  font-family: 'VT323', monospace;
}
html {
  background: #08141e;
}
body {
  margin: 0;
  padding: 0;
  color: #f6d6bd;
  font-size: 1.5rem;
}
p {
  margin: 0;
  padding: 0;
}
ul {
  padding: 0;
  margin: 0;
  list-style: none;
}
a {
  color: #f6d6bd;
}
a:hover {
  background: #f6d6bd;
  color: #08141e;
}
nav {
  border-bottom: solid #0f2a3f 2px;
  display: flex;
  justify-content: space-between;
  margin-bottom: calc(1.5rem * 1.5);
}
nav ul {
  display: flex;
}
nav a {
  text-decoration: none;
  text-transform: uppercase;
  padding: 0 0.5rem;
  color: #997577;
}
nav a:hover {
  color: #f6d6bd;
  background: #08141e;
}
nav a.active {
  color: #c3a38a;
  background: #0f2a3f;
  pointer-events: none;
  cursor: default;
}
.Home h1 {
  visibility: hidden;
  position: absolute;
}
.Home .logo-and-timestamp {
  display: grid;
  grid-template-columns: 50% 50%;
  grid-row-gap: 0.75rem;
  width: calc(100vw - 3rem);
  max-width: 46rem;
  margin: 0 auto 3rem auto;
}
.Home .logo-and-timestamp img {
  width: 100%;
  display: inline-block;
  grid-column: 1/3;
  image-rendering: pixelated;
}
.Home .logo-and-timestamp span {
  font-size: 1rem;
  color: #997577;
}
.Home .logo-and-timestamp span:nth-child(3) {
  text-align: right;
}
.Home p {
  width: calc(100vw - 3rem);
  max-width: 46rem;
  margin: 3rem auto;
  color: #997577;
}
.Home p.thumbnails {
  margin: 0 auto;
  display: flex;
  justify-content: space-evenly;
  flex-wrap: wrap;
}
.Home p.thumbnails img {
  width: calc(128px - 8px);
  padding: 0.75rem 4px;
  image-rendering: pixelated;
}
.Home p.feature {
  display: grid;
  grid-template-columns: auto 30rem;
  grid-column-gap: 1.5rem;
  margin-bottom: 6rem;
}
@media screen and (max-width: 44rem) {
  .Home p.feature {
    grid-template-columns: 100%;
    grid-column-gap: 0;
    grid-row-gap: 1.5rem;
  }
}
.Home p.feature span {
  font-size: 1.5rem;
}
.Home p.feature span:before {
  content: 'â–ª ';
}
.Home p.feature img {
  width: 100%;
  image-rendering: pixelated;
}
@media screen and (max-width: 44rem) {
  .Home p.feature img {
    max-width: 32rem;
    margin: 0 auto;
  }
}
